import { getParamNameValueString } from 'utils/common'
import {FEED_END_POINT} from 'constants';

const getURLParams = () => {
  const urlSearchParams = new URLSearchParams(window.location.search)
  return Object.fromEntries(urlSearchParams.entries())
}

export const getWidgetFeedUrl = (client, type, id) => {
  let jeanieEnvSuffix = ''
  let jeanieDBEnvParam = ''
  let jeanieCPParam = ''
  const URLParams = getURLParams()

  const queryJeanieEnv = URLParams.jeanie_env
  const queryJeanieDBEnv = URLParams.jeanie_db_env
  const queryJeanieCP = URLParams.jeanie_cache_policy
  if (!__PROD__) {
    // normal jeanie user
    jeanieDBEnvParam = 'db_env=drafts'

    if (queryJeanieEnv === 'staging') {
      jeanieEnvSuffix = '-stg'
    }

    if (queryJeanieDBEnv === 'published') {
      jeanieDBEnvParam = 'db_env=published'
    }
    if (queryJeanieCP) {
      jeanieCPParam = `&cache_policy=${queryJeanieCP}`
    }
  }
  return `https://jeanie${jeanieEnvSuffix}.symplor.com/v1/feeds/${client}/${type}${
    id ? `/${id}` : ''
  }?${jeanieDBEnvParam}${jeanieCPParam}`
}

export const getFeedURLFromOptions = (options = {}) => {
  const { filters = {}, type, id, client } = options

  let url = getWidgetFeedUrl(client, type, id)
  const keys = Object.keys(filters)
  keys.forEach((key) => {
    if (typeof filters[key] !== 'undefined') {
      if (key === 'pcheck') {
        if (filters[key] === 'yes' || filters[key] === true) {
          url += getParamNameValueString(key, 'yes', url)
        }
      }
      if (key === 'country') {
        url += getParamNameValueString(
          key,
          filters[key].toString().toLowerCase(),
          url
        )
      } else {
        url += getParamNameValueString(
          key.toString().toLowerCase(),
          filters[key],
          url
        )
      }
    }
  })

  return url
}

export const getStaticFeedURL = (options = {}) => {
  const { symplorSlot, symplorUser } = options
  const url = `${FEED_END_POINT}/${symplorUser}/${symplorSlot}.json`
  
  return url
}


