/* eslint-disable no-underscore-dangle */
/* eslint-disable import/prefer-default-export */
import fetcher from 'utils/fetcher'
import library from 'utils/library'
import logger from 'utils/logger'
import { mergeDeep, getSafe, loadScript } from 'utils/common'
// import Validator from './validator'
import { getStaticFeedURL } from 'utils/jeanie'

// eslint-disable-next-line import/prefer-default-export
const _loadWidget = (element, options) =>
  // #todo: need to check the risk of disabling this rule
  // eslint-disable-next-line no-async-promise-executor
  new Promise(async (resolve, reject) => {
    const { props = {} } = options

    // if (!Validator.isValidLoadWidgetCall(element, options)) {
    //   reject()
    //   return
    // }

    // multiple parallel calls could be made to render a widget
    const requiredPromises = []

    // feed response from jeanie, which also tells which library component is required
    let { widgetFeed } = options
    // component loaded via library
    let widgetComponent

    const feedURL = getStaticFeedURL(options)

    try {
      // wait for feed response which tells which component to load
      if (!widgetFeed) {
        await fetcher.getJSON(feedURL).then((feedResponse) => {
          widgetFeed = feedResponse
        })
      }

      // if widgetFeed doesn't specify the type (component name as per library)
      // then the component can't be loaded/rendered
      if (!widgetFeed.type) {
        // #todo : log error
        reject()
        return
      }

      // // load and set data in widgetFeed Response if dataURL value is present in widgetFeed
      // // and the data field is absent
      // const dataURL = options.dataURL || widgetFeed.dataURL
      // if (dataURL) {
      //   requiredPromises.push(
      //     fetcher.getJSON(dataURL).then((data) => {
      //       let resData = data
      //       widgetFeed.data = widgetFeed.data || {}
      //       const dataPath = options.dataPath || widgetFeed.dataPath
      //       if (options.dataPath) {
      //         resData = getSafe(resData, dataPath)
      //       }
      //       if (options.dataAliasKey) {
      //         widgetFeed.data[options.dataAliasKey] = resData
      //       } else {
      //         widgetFeed.data = resData
      //       }
      //       mergeDeep(widgetFeed.data, data)
      //     })
      //   )
      // }

      // if error in feed / data
      if (widgetFeed.error || (widgetFeed.data && widgetFeed.data.error)) {
        reject()
      }

      const mergedProps = {}
      mergeDeep(mergedProps, widgetFeed, props)

      let widgetIframe

      if (options.symplorSkipIframe !== 'yes') {
        widgetIframe = document.createElement('iframe')
        widgetIframe.height = 250
        widgetIframe.width = '100%'
        widgetIframe.style = 'border: none;'
        element.append(widgetIframe)
      }

      let win = window
      let doc = document

      if (options.symplorSkipIframe !== 'yes' && widgetIframe) {
        win = widgetIframe.contentWindow
        doc = widgetIframe.contentDocument
      } else {
        mergedProps.symplorSkipIframe = "yes";
      }

      // load the library component needed for this widget
      requiredPromises.push(
        library.loadComponent(widgetFeed.type, doc).then(() => {
          widgetComponent = win.symplor.components[widgetFeed.type]
        })
      )

      // wait for all required promises to be resolved calling Renderer
      await Promise.all(requiredPromises)

      const { html, styles, onLoad } = widgetComponent(mergedProps)

      if (
        !doc.querySelector(`[data-symplor-css-widget="${widgetFeed.type}"]`)
      ) {
        const styleEl = doc.createElement('style')
        styleEl.setAttribute('data-symplor-css-widget', widgetFeed.type)
        styleEl.innerHTML = styles
        doc.head.appendChild(styleEl)
      }

      if (options.symplorSkipIframe !== 'yes') {
        doc.body.innerHTML = `<div id="symplor_widget">${html}</div>`
      } else {
        doc.getElementById('symplor_widget').innerHTML = html
      }

      loadScript(
        'https://www.googletagmanager.com/gtag/js?id=G-M9SNMLX6QM',
        undefined,
        undefined,
        doc
      )

      const gaInlineScriptEl = doc.createElement('script')
      gaInlineScriptEl.innerHTML = `window.dataLayer = window.dataLayer || [];
      function gtag(){window.dataLayer.push(arguments);}
      gtag('js', new Date());
      gtag('config', 'G-M9SNMLX6QM');`
      doc.head.appendChild(gaInlineScriptEl)

      win.gtag('event', 'sdk', {
        action: 'widget_loaded',
        label: 'widgetFeed.type',
        pp_url: window.location?.href,
      })

      setTimeout(() => {
        const widgetEl = doc.getElementById('symplor_widget')
        if (typeof onLoad === 'function') {
          onLoad(options.symplorSkipIframe !== 'yes' ? widgetIframe : widgetEl, feedURL)
        }
        if (options.symplorSkipIframe !== 'yes') {
          setTimeout(() => {
            widgetIframe.height = widgetEl.scrollHeight
            if (win.ResizeObserver) {
              new win.ResizeObserver(() => {
                widgetIframe.height = widgetEl.scrollHeight
              }).observe(widgetEl)
            }
          }, 0)
        }
      }, 0)

      resolve(mergedProps)
    } catch (e) {
      reject()
      logger.log(
        `Error in fetching feed Url or in loading library component--->${e}`
      )
    }
  })

export const initWidgets = (widgetDomEL, widgetOptionsObj) => {
  const widgetELs = widgetDomEL
    ? [widgetDomEL]
    : document.querySelectorAll('[data-symplor-widget]')
  const widgetPromises = []

  widgetELs.forEach((widgetEL) => {
    const widgetOptions = widgetOptionsObj || widgetEL.dataset
    // #todo : handle individual widget/EL init skip
    // if(widgetOptions.symplorWidgetsSkipAutoInit === 'yes'){
    //   return;
    // }
    widgetPromises.push(
      _loadWidget(widgetEL, widgetOptions)
        .then(() => {
          widgetEL.setAttribute('data-symplor-processed', 'yes')
        })
        .catch((err) => {
          widgetEL.setAttribute('data-symplor-processed', 'yes')
          widgetEL.setAttribute('data-symplor-error', 'yes')
          logger.log(err)
        })
    )
  })

  return widgetPromises
}

let skipAutoInit = false

if (
  window?.symplor_widgets?.skipAutoInit ||
  document.body.getAttribute('data-symplor-widgets-skip-auto-init') === 'yes'
) {
  skipAutoInit = true
}

if (!skipAutoInit) {
  initWidgets()
}
